.item {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 20px;
}


.item__box {
  padding: 10px 0;
  width: 48%;
}

.item__img-box {
  margin-right: 40px;
}

.item__img {
  width: 200px;
  height: 100%;
}

.item__title {
  margin: 0;
  margin-bottom: 10px;
  color: var(--blue);
  font-size: 20px;
}

.item__author {
  margin-bottom: 10px;
}

.item__price {
  font-weight: bold;
  margin-bottom: 20px;
}

.item__btn {
  
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 20px;
  border-bottom: 1px solid var(--border-color);
}

.header__logo {
  text-decoration: none;
}

.header__logo-link {
  font-family: 'Playfair Display', sans-serif;
  font-size: 30px;
  font-weight: 700;
  color: inherit;
  text-decoration: none;
}

.error-indicator {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  color: #c78f22;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}
*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

:root {
  --dark-blue: #0069D9;
  --blue: #007bff;
  --light-gray: #dddddd;
  --border-color: var(--light-gray);
}

body {
  margin: 0;
  min-width: 320px;
  font-size: 20px;
  font-family: "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

.btn {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  color: inherit;
  font: inherit;
  text-align: left;
  cursor: pointer;
}

.btn-blue {
  background-color: #007bff;
  background-color: var(--blue);
  color: #ffffff;
}

.btn-blue:hover,
.btn-blue:focus {
  background-color: #0069D9;
  background-color: var(--dark-blue);
}

@media  (min-width: 1550px) {
  .container {
    width: 1550px;
    margin: 0 auto;
  }
}
